// store/index.js 
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);//vue

//Vuex.Store 
const store = new Vuex.Store({
	// 
	state:{
		token: "",
		refresh: "",
		userInfo: {},
		// 
		isAuth: false,
		// 
		pos: ""
	},
	// 
	getters:{
		
	},
	// 
	mutations:{
		// 
		saveLoginStatus(state, info){
			// console.log('vuex:',info)
			state.isAuth = true
			state.token = info.token
			// uniapp
			uni.setStorage({
				key: "isAuth",
				data: true
			})
			uni.setStorageSync("token",info.token)
			uni.setStorage({
				key: "refresh",
				data: info.refresh
			})
		},
		// 
		saveUserInfo(state,user){
			state.userInfo = user
			uni.setStorage({
				key:"userinfo",
				data:user
			})
		},
		// 
		logOut(state){
			// vuex
			state.isAuth = false
			state.userInfo = {}
			//uni-appStorage
			uni.clearStorage()
			// 
			uni.switchTab({
				url:"/pages/index/index"
			})
		}
	},
	// 
	actions:{
		
	}
})
export default store
