// 
const http = {
	// baseurl
	baseUrl: 'http://127.0.0.1:8000',
	// 
	request(config){
		// config
		config = beforeRequest(config)
		config.url = this.baseUrl + config.url
		// cPromise
		return new Promise((resolve,reject) => {
			uni.request(config)
				.then(res => {
					let [error,resp] = res
					const response = beforeResponse(resp)
					resolve(response)
				})
				.catch(err => {
					errorHandle()
					reject(err)
				})
		})
	},
	get(url,data,auth){
		// url:
		// data:
		// auth:token(true or false)
		return this.request({
			url:url,
			data:data,
			auth:auth,
			method:"GET"
		})
	},
	post(url,data,auth){
		// url:
		// data:
		// auth:token(true or false)
		return this.request({
			url:url,
			data:data,
			auth:auth,
			method:'POST'
		})
	},
	delete(url,auth){
		// url:
		// auth:token(true or false)
		return this.request({
			url:url,
			auth:auth,
			method:'DELETE'
		})
	},
	put(url,data,auth){
		// url:
		// data:
		// auth:token(true or false)
		return this.request({
			url:url,
			data:data,
			auth:auth,
			method:'PUT'
		})
	}
}
// 
const beforeRequest = (config) => {
	// 
	console.log('', config)
	config.header = {}
	// token
	if(config.auth){
		//token
		if(uni.getStorageSync('token')){
			config.header['Authorization'] = 'Bearer '+ uni.getStorageSync('token')
		}else{
			// 
			uni.navigateTo({
				url:'/pages/user/login'
			})
		}
	}
	return config
}


// 
const beforeResponse = (response) =>{
	// 
	if(response.statusCode!==200 &&response.statusCode!==201 && response.statusCode!==204){
		// 
		if(response.data.error){
			// 
			uni.showToast({
				title: response.data.error.toString(),
				duration: 1000,
				icon:"error"
			});
		}
	}
	console.log('', response)
	return response
}


//
const errorHandle = (err) =>{
	console.log('', err)
}


export default http